import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
      //是否能切换下一个
      clickNextSwitch:true,
      // 图片显示
      imageShow:false,
      // 视频显示
      videoShow:false,
      // 视频地址
      videoUrl:'',
      //图片地址
      imgUrl:'',
      //背景音乐地址
      audioBGMUrl:'',
      //背景音乐是否播放
      audioBGMUrlPlay:false,
      // 当前执行的文件名
      StoryTextFile:'',
      // 执行到第几行文本
      StoryTextRowID:0,
      // 背景开始动画
      AnimBefore:{"opacity":0},
      //背景结束动画
      AnimAfter:{"opacity":1},
      // 是否是css动画
      IsCssAnim:false,
      //css动画
      AnimCss:"",
      // 动画持续时间
      AnimDuration:500,
      //循环次数
      AnimLoop:1,
      // 人物立绘左侧
      RoleLeft:{},
      //人物立绘左侧脸部
      RoleLeftFace:{},
      //人物立绘左侧表情
      RoleLeftLook:{},
      RoleCenter:{},
      RoleCenterFace:{},
      RoleCenterLook:{},
      RoleRight:{},
      RoleRightFace:{},
      RoleRightLook:{},
      //人物立绘的语音
      AudioRoleUrl:'',
      //人物说的话
      RoleText:'',
      //人物的名字
      RoleName:'',
      //背景偏移
      ImgTrans:'',
      //人物偏移
      RoleTransCenter:'',
      RoleTransLeft:'',
      RoleTransRight:'',
      TextBoardShow:false,
      RoleShow:false,
      nextDuration:0
  },
  mutations: {
      IsImageShow(state,value){
          state.imageShow=true
          state.videoShow=false
          state.videoUrl=''
          state.imgUrl=value
      },
      IsVideoShow(state,value){
          state.imageShow=false
          state.videoShow=true
          state.imgUrl=''
          state.videoUrl=value
      },
  },
  actions: {
  },
  modules: {
  }
})
