import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import {Button, Image, Input, Row, Col, Message,MessageBox ,Checkbox,CheckboxButton,Slider,InputNumber,Select,Option} from 'element-ui';
import cssanimation from './statics/cssanimation.min.css'
import 'animate.css';
import Velocity from 'velocity-animate'
Vue.prototype.$confirm = MessageBox.confirm
import axios from 'axios'
Vue.use(Button)
Vue.use(Image)
Vue.use(Input)
Vue.use(Row)
Vue.use(Col)
Vue.use(Checkbox)
Vue.use(CheckboxButton)
Vue.use(Slider)
Vue.use(InputNumber)
Vue.use(Select)
Vue.use(Option)


Vue.config.productionTip = false

new Vue({
    beforeCreate() {
        Vue.prototype.$bus = this //安装全局事件总线，$bus就是当前应用的vm
        Vue.prototype.$message = Message
        Vue.prototype.$http = axios
    },
  router,
  store,
  render: h => h(App)
}).$mount('#app')
